import { h, Component } from 'preact'
import style from './style.css'

export default class Button extends Component {
  render(props) {
    return (
      <button className={`${style.button} ${props.className} mdl-button mdl-js-button mdl-button--raised mdl-button--colored`}>
        {props.children}
      </button>
    )
  }
}



