import { h, Component } from 'preact'
import style from './style.css'

export default class Chip extends Component {
  render(props) {
    return (
      <span className={`${style.chip} ${props.className} mdl-chip`}>
        <span className="mdl-chip__text">{props.children}</span>
      </span>
    )
  }
}



