import { h, Component } from 'preact'
import Card from '../../components/card'
import Anchor from '../../components/anchor'
import Chip from '../../components/chip'
import Button from '../../components/button'
import cases from './cases'
import style from './style.css'

export default class Portfolio extends Component {
  constructor() {
    super()
    this.renderCase = this.renderCase.bind(this)
  }

  renderCase(item) {
    const title = item.url
      ? <Anchor href={item.url}>{item.title}</Anchor>
      : <span>{item.title}</span>
    const chips = (
      <div>
        {item.techs.map((tech, index) => <Chip key={index}>{tech}</Chip>)}
      </div>
    )
    const supportingText = item.description
    return (
      <Card title={title} chips={chips} supportingText={supportingText}></Card>
    )
  }

  render() {
    return (
      <div className={style.portfolio}>
        <h3>Portfolio</h3>
        <div>
          {cases.map(item => this.renderCase(item))}
        </div>
        <div>
          <Anchor className={style.viewResumeLink} href="/assets/documents/resume.pdf">
            <Button className={style.viewResumeButton}>View Resume</Button>
          </Anchor>
        </div>
      </div>
    )
  }
}



